@font-face {
  font-family: 'aicon';
  src:  url('fonts/aicon.eot?adoto8');
  src:  url('fonts/aicon.eot?adoto8#iefix') format('embedded-opentype'),
    url('fonts/aicon.woff2?adoto8') format('woff2'),
    url('fonts/aicon.ttf?adoto8') format('truetype'),
    url('fonts/aicon.woff?adoto8') format('woff'),
    url('fonts/aicon.svg?adoto8#aicon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="aicon-"], [class*=" aicon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'aicon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

